<template>
  <div class="hello">
    <h2>Tereny</h2>
    <h3>Dodaj nowy teren</h3>
    <form @submit.prevent="addT">
    <input type="number" placeholder="Numer" v-model="newT.number">
    <input type="text" placeholder="Opis" v-model="newT.desc">
    <label>Telefoniczny
      <input v-model="newT.tel" type="checkbox" name="info" value="tel">
    </label>
    <input type="submit" value="Dodaj">
    </form>
    <h3>Lista terenów ({{tNumber}})</h3>
    <input v-model="searchQuery" type="text" placeholder="Szukaj w opisach">
    <label>Pokaż usunięte<input type="checkbox" v-model="showAll"></label>
  <ul>
    <li v-for="(t, index) in filteredT" :key="index">
      <div class="width2">
        <span>{{t.number }}</span>
        <span v-if="t.tel">
          {{ (t.tel)? "☎":"" }}
        </span>
      </div>
      {{ t.desc }}
      <div>
        <button @click="editT(t)">edycja</button>
        <button v-if="t.active" @click="t.active=false">usuń</button>
        <button v-if="!t.active" @click="t.active=true">przywróć</button>
      </div>
    </li>
  </ul>
  <button @click="showNumber += 10">Pokaż więcej</button>
  </div>
</template>

<script>
export default {
  name: "Terr",
  props: [
      "territories",
      "addT",
      "newT",
      "editT",
      "editedT",
  ],
  data() {
    return {
      searchQuery: "",
      showAll: false,
      showNumber: 10,
    }
  },
  computed: {
    tNumber() {
      return this.territories.length
    },
    filteredT() {
      return this.territories.filter(t => {
          return (this.showAll? !t.active : t.active) & (t.desc.toLowerCase().includes(this.searchQuery.toLowerCase()))
        }).slice(0, this.showNumber)
      },    
    },
  };
</script>

<style>

</style>



