<template>
<div>
  <h2>Info</h2>
  
  <p>
    Fill DB with stupid data, use only once.
    
    <button @click.once="fillDB">fill db</button>
  </p>
  <hr>
  <h3>TODO</h3>
  <ul>
    <li>t sort free <strong> fix sorting </strong></li>
    <li>t sort given 100 days ago</li>
    <li>edit p&t</li>
    <li>design...</li>
  </ul>
</div>
</template>

<script>
export default {
  name: "Info",
  props: [
    "territories",
    "publishers"
  ],
    methods: {
    fillDB() {
      const tArr = this.territories
      const pArr = this.publishers
      for (let i=1; i<201; i++){
        setTimeout(function(){
          let x = i%2==0 ? "C" : "B"
          let newItem = {
            id: new Date().getTime(),
            name: "Imię " + i,
            surname: "Nazwisko" + x,
            active: true,
            has: []
          }
          pArr.push(newItem)
          
        },1)
      }
      for (let i=1; i<301; i++){
        setTimeout(function(){
          let x = i%2==0 ? "A" : "B"
          let newItem = {
            id: new Date().getTime(),
            number: i,
            desc: x+ " opis " + i,
            tel: false,
            active: true,
            free: true,
            given: [],
            recieved: []
          }
          tArr.push(newItem)

        },1)
      }
    },
  }
}
</script>



