<template>
<div>
  <h2>Przydział</h2>
  <h3>Lista wolnych terenów ({{freeNumber}})</h3>
  <input v-model="searchT" type="text" placeholder="Szukaj w opisach">
  <div class="modal" v-show="showP">
    <h3>Lista głosicieli</h3>
    <input v-model="searchP" type="text" placeholder="Szukaj w nazwiskach">
    <label>Data przydziału: 
      <input type="date" :value="nDate && nDate.toISOString().split('T')[0]"
        @input="nDate = $event.target.valueAsDate">
    </label>
    <button @click="cancel()">Anuluj</button>
    <ul>
    <li v-for="p in filteredP" :key="p.id">
        {{ p.name }}
        {{ p.surname }}
        <div>
          <button @click="give(p)">Wybierz</button>
        </div>
    </li>
    </ul>
  </div>
  <ul>
    <li v-for="t in filteredT" v-bind:key="t.id">
      <div class="width2">
        <span>{{t.number }}</span>
        <span v-if="t.tel">
          {{ (t.tel)? "☎":"" }}
        </span>
      </div>
      {{ t.desc }}
      <div v-if="t.recieved.length">
        Opracowany {{t.recieved.length}} razy, 
        statnio {{Math.floor(((new Date().getTime()) - t.recieved[t.recieved.length-1] ) / 86400000) }} dni temu
      </div>
      <button @click="giveHandler(t)">przydziel</button>
    </li>
  </ul>  
  <button @click="showNumber += 10">Pokaż więcej</button>
</div>
</template>

<script>
export default {
  name: "Give",
  props: [
    "territories",
    "publishers"
  ],
  data() {
    return {
      searchT: "",
      searchP: "",
      showP: false,
      currentTid: "",
      nDate: new Date(),
      showNumber: 10,
    }
  },
  methods: {
    giveHandler(t) {
      this.showP = true
      this.currentTid = t.id
    },
    give(p){
      let id = this.currentTid
      let t = this.territories.find(function(el){
        return el.id == id
      })
      t.given.push({
        date: this.nDate.getTime(),
        publisherId: p.id
      })
      t.free = false
      p.has.push({
        date: new Date().getTime(),
        tId: this.currentTid
      })

      this.cancel()
    },
    cancel() {
      this.nDate = new Date()
      this.currentTid = ""
      this.showP = false
      this.searchP = ""
    },
  },
  computed: {
  freeNumber() {
    return this.territories.filter (t => {
        return t.active & t.free }).length
  },
  filteredT() {
    return this.territories.filter(t => {
        return (
          t.active &
          t.free &
          (t.desc.toLowerCase().includes(this.searchT.toLowerCase()))
          )})
          .sort(function(a, b){
            if (a.recieved.length & b.recieved.length)
              return a.recieved[a.recieved.length-1] - b.recieved[b.recieved.length-1]}) 
          .slice(0, this.showNumber)
    },
  filteredP() {
    return this.publishers.filter(p => {
        return  p.active & (p.surname.toLowerCase().includes(this.searchP.toLowerCase()))
      }).slice(0, 5)
    },    
  },    
};
</script>

<style>

</style>



