<template>
  <div class="hello">
    <h2>Zwrot</h2>
    <label>
      Podaj numer: 
      <input v-model="searchNumber" type="number" placeholder="Numer terenu">
    </label>
    <label>Data opracowania: 
      <input type="date" :value="nDate && nDate.toISOString().split('T')[0]"
        @input="nDate = $event.target.valueAsDate">
    </label>
    <ul>
      <li v-for="t in filteredT" v-bind:key="t.id">
        <div class="width2">
          <span>{{t.number }}</span>
          <span v-if="t.tel">
            {{ (t.tel)? "☎":"" }}
          </span>
        </div>
        {{ t.desc }}
        <button @click="recieve(t)">zwrot</button>
      </li>
    </ul>
      <h3>Lista terenów ({{tNumber}})</h3>
    <input v-model="searchQuery" type="text" placeholder="Szukaj w opisach">
    <div v-if= "warningFilteredT.length">
      <h3>Lista terenów przydzielonych ponad 100 dni temu</h3>
      <ul>
        <li v-for="(t, index) in warningFilteredT" :key="index">
          <div class="width2">
            <span>{{t.number }}</span>
            <span v-if="t.tel">
              {{ (t.tel)? "☎":"" }}
            </span>
          </div>
          {{ t.desc }}
          <div>
            Przydzielony {{Math.floor((new Date().getTime() - t.given[t.given.length-1].date) / 86400000)}} dni temu
          </div>
          <button @click="recieve(t)">zwrot</button>
        </li>
      </ul>
    <button v-if="warningFilteredT.length>10" @click="showNumber += 10">Pokaż więcej</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Recieve",
  props: ["territories", "publishers"],
  data() {
    return {
      searchNumber: "",
      nDate: new Date(),
      showNumber: 10,
      searchQuery: "",
      tNumber: "",
    };
  },
  methods: {
    recieve(t) {
      t.recieved.push(this.nDate.getTime())
      t.free = true
      this.updateP(t)
      this.nDate = new Date()
      this.searchNumber = ""
    },
    updateP(t) {
      const pArr = this.publishers
      for (let i = 0; i < pArr.length; i++) {
        for (let j = 0; j <pArr[i].has.length; j++)
          if ((pArr[i].has[j].tId) == t.id) {
            pArr[i].has.splice(j, 1)
          }
      }      
    }
  },
  computed: {
    filteredT() {
      return this.territories.filter(t => {
        return t.number == +this.searchNumber & t.active & !t.free
      })
    },
    warningFilteredT() {
      let days = new Date().getTime() - (86400000 * 100)
        return this.territories.filter(t => {
            if (!t.free) return t.given[t.given.length-1].date < days})
          .slice(0, this.showNumber)
    },    
  }
}
</script>

<style>

</style>



