<template>
  <div id="app">
    <h1 class="logo">THelper</h1>
    <nav class="main-nav">
      <button @click="currentComponent='Give'">Przydział</button>
      <button @click="currentComponent='Recieve'">Zwrot</button>
      <button @click="currentComponent='Terr'">Tereny</button>
      <button @click="currentComponent='Publ'">Głosiciele</button>
      <button @click="currentComponent='Backup'">Kopia zap.</button>
    </nav>
    <component
      :territories="territories"
      :publishers="publishers"   
      :addT="addT" 
      :newT="newT" 
      :editT="editT" 
      :editedT="editedT" 
      :addP="addP" 
      :newP="newP" 
      :editP="editP" 
      :editedP="editedP" 
      :is="currentComponent">
    </component>
  </div>
</template>

<script>
import Give from "./components/Give";
import Recieve from "./components/Recieve";
import Terr from "./components/Terr";
import Publ from "./components/Publ";
import Backup from "./components/Backup";
import Info from "./components/Info";

export default {
  data() {
    return {
      currentComponent: Info,
      territories: [],
      publishers: [],
      newP: {},
      newT: {},
      editedP: {},
      editedT: {},
      recievedNumber: "",
    };
  },
  name: "App",
  components: {
    Give,
    Recieve,
    Terr,
    Publ,
    Backup,
  },
  methods: {
    addP() {
      this.publishers.push({
        id: new Date().getTime(),
        name: this.newP.name,
        surname: this.newP.surname,
        active: true,
        has: []
      });      
      this.newP = {};
    },
    editP(p){
      //moze assign wystarczy?
      this.editedP= JSON.parse(JSON.stringify(p))
      console.log(this.editedP)
    },
    addT() {
        this.territories.push({
          id: new Date().getTime(),
          number: +this.newT.number,
          desc: this.newT.desc,
          tel: this.newT.tel | false,
          active: true,
          free: true,
          given: [],
          recieved: []
        });
      this.newT = {};
    },
    editT(t){
      this.editedT= JSON.parse(JSON.stringify(t))
      console.log(this.editedT)

    },
  },
  mounted() {
    console.log("App mounted!");
    if (localStorage.getItem("territories"))
      this.territories = JSON.parse(localStorage.getItem("territories"));
    if (localStorage.getItem("publishers"))
      this.publishers = JSON.parse(localStorage.getItem("publishers"));
  },
  watch: {
    territories: {
      handler() {
        console.log("Territories changed!");
        localStorage.setItem("territories", JSON.stringify(this.territories));
      },
      deep: true
    },
    publishers: {
      handler() {
        console.log("Publishers changed!");
        localStorage.setItem("publishers", JSON.stringify(this.publishers));
      },
      deep: true
    }
  }
};
</script>

<style>
body {
  margin: 0;
  padding: 0;
}
#app {
  padding: 0.5em;
  max-width: 800px;
  margin: 0 auto;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #515f6d;
  background: #fafafa;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.6em 0;
  border-bottom: 1px dashed #e6e6e6;
}
li span {
  display: inline-block;
  text-align: center;
  line-height: 1.2em;
  font-size: 0.6rem;
  background: #e2ebff;
  border-radius: 50%;
  width: 1.2em; 
  height: 1.2em;
  padding: 1.2em;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1)
}
input {
  padding: 0.2em;
  margin: 0.5em;
  font-size: 1rem;
}
input[type="submit"],
button {
  color: #515f6d;
  margin: 0.5em;
  padding: 0.3em 1em 0.1em;
  font-size: 1rem;
  text-transform: uppercase;
  background: #e2ebff;
  border: none;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1)
  }
.red{
  background: #ff2212;
  color: #060742;
}
.logo{
  position: absolute;

}
.main-nav{
  text-align: right;
}
.width2 {
  width: 7.2em;
}
.modal {
  background: #ffe89d;
  border: 4px solid #f5bd2f;
}
@media(max-width: 800px) {
  .logo{
    display: none;
  }
}
</style>



