<template>
  <div>
    <h2>Kopia zapasowa</h2>
    <p>Dane się przechowywane w pamięci przeglądarki (localStorage).</p>
    
    <button @click="localStorageBackup">Stwórz kopię zapasową</button>
    <button @click="localStorageRestore">Przywróć kopię zapasową</button>

    <h3>Uważaj na ten przycisk!</h3>
    <button class="red" @click="localStorageClear">Wyczyść wszystkie dane</button>
  </div>
</template>

<script>
export default {
  name: "Backup",
  data() {
    return {};
  },
  methods: {
  localStorageBackup() {
    var backup = {};
    for (var i = 0; i < localStorage.length; i++) {
      var key = localStorage.key(i);
      var value = localStorage.getItem(key);
      backup[key] = escape(encodeURIComponent(value));
    }
    var json = JSON.stringify(backup);
    var base = btoa(json);
    var href = 'data:text/javascript;charset=utf-8;base64,' + base;
    var link = document.createElement('a');
      link.setAttribute('download', 'backup.json');
    link.setAttribute('href', href);
    document.querySelector('body').appendChild(link);
    link.click();
    link.remove();
  },

  localStorageRestore() {
    var t = document.createElement('div');
    var a = document.createElement('a');
    a.appendChild(document.createTextNode('X'));
    a.setAttribute('href', '#');

    a.style.position = 'absolute';
    a.style.top = '10px';
    a.style.right = '10px';
    a.style['text-decoration'] = 'none';
    a.style.color = '#fff';
    t.appendChild(a);
    a.onclick = function() {
        t.remove();
    };
    t.style.width = '50%';
    t.style.position = 'absolute';
    t.style.top = '25%';
    t.style.left = '25%';
    t.style['background-color'] = 'gray';
    t.style['text-align'] = 'center';
    t.style.padding = '50px';
    t.style.color = '#fff';
    t.style['z-index'] = 10000;

    var l = document.createElement('input');
    l.setAttribute('type', 'file');
    l.setAttribute('id', 'fileinput');
    l.onchange = function(e) {
        t.remove();
        var f = e.target.files[0];
        if (f) {
            var reader = new FileReader();
            reader.onload = function(e) {
                var text = e.target.result;
                var backup = JSON.parse(text);
                for (var key in backup){
                  var value = decodeURIComponent(unescape(backup[key]));
                  window.localStorage.setItem(key, value);
                }
                alert('Imported ' + Object.keys(backup).length + ' items from backup.')
            };
            reader.readAsText(f);
        } else {
          alert('Failed to load file');
        }
    };
    var a = document.createElement('h3');
    a.appendChild(document.createTextNode('Wybierz plik zawierający kopię zapasową'));
    t.appendChild(a);
    t.appendChild(l);
    document.querySelector('body').appendChild(t);
  },

  localStorageClear() {
    if(window.confirm('Czy naprawdę chcesz usunąć wszystkie ' + localStorage.length + ' zbiory danych (localStorage) tej strony?')) {
      localStorage.clear();
    }
  },
  },
};
</script>

<style>

</style>



