<template>
  <div class="hello">
    <h2>Głosiciele</h2>
    <h3>Dodaj nowego głosiciela</h3>
    <form @submit.prevent="addP">
    <input type="text" placeholder="Imię" v-model="newP.name">
    <input type="text" placeholder="Nazwisko" v-model="newP.surname">
    <input type="submit" value="Dodaj">
    </form>
    <h3>Lista głosicieli ({{pNumber}})</h3>
    <input v-model="searchQuery" type="text" placeholder="Szukaj w nazwiskach">
    <label>Pokaż usuniętych<input type="checkbox" v-model="showAll"></label>
    <ul>
      <li v-for="p in filteredP" :key="p.id">
        {{ p.name }}
        {{ p.surname }}
        <div>
          <button @click="editP(p)">edycja</button>
          <button v-if="p.active" @click="p.active=false">usuń</button>
          <button v-if="!p.active" @click="p.active=true">przywróć</button>
        </div>
      </li>
    </ul>
    <button @click="showNumber += 10">Pokaż więcej</button>
  </div>
</template>

<script>
export default {
  name: "Publ",
  data() {
    return {
      searchQuery: "",
      showAll: false,
      showNumber: 10,
    }
  },
  props: [
    "publishers",
    "newP",
    "addP",
    "editP",
    "editedP",
  ],
  computed: {
  pNumber() {
    return this.publishers.length
  },
  filteredP() {
    return this.publishers.filter(p => {
        return (this.showAll? !p.active : p.active) & (p.surname.toLowerCase().includes(this.searchQuery.toLowerCase()))
      }).slice(0, this.showNumber)
    },    
  },
};
</script>

<style>

</style>



